import Vue from 'vue'
import Router from 'vue-router'

import index from '@/views/index'
import classes from '@/views/classes'
import classroom from '@/views/classroom'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
      name: 'index',
      component: index,
      children:[
      {
        path:'login',
        name:'login',
        component:index.login
      },
      {
        path:'register',
        name:'register',
        component:index.register,
      },
      {
        path:'register/:type',
        name:'registerTyped',
        component:index.registerTyped
      }
      ]
    },
    {
      path: '/classes',
        name: 'classes',
        redirect:'/classes/main',
        component:classes,
        children:[
          {
            path:'main',
            name:'classes-main',
            component:classes.main
          },
          {
            path:'setting',
            redirect: 'setting/userInfo',
            name:'classes-setting',
            component:classes.setting,
            children:[
              {
                path:'userInfo',
                name:'classes-setting-userInfo',
                component:classes.setting.userInfo
              }
            ]
          }
        ]
    },
    {
      path:'/classroom',
      name:'classroom',
      redirect:'/classroom/homework',
      component:classroom,
      children:[
        {
          path:'homework',
          name:'homework',
          component:classroom.homework,

        },
        {
          path:'submithomework',
          name:'submithomework',
          component:classroom.submithomework,

        },
        {
          path:'topic',
          name:'topic',
          component:classroom.topic,

        },
        {
          path:'sharefile',
          name:'sharefile',
          component:classroom.sharefile,
        },
      ]
    }
  ]
})



