<template>
  <div id = "classroom">
    <transition name="slide-fade">
    <header @click.prevent = "isDrawerOut = false" v-show="headerShow">
      <!--头部菜单-->
      <div id = "menubar">
        <div>
        </div>
        <div id = "user-option">
          <span><router-link to="/login"><Badge :value="200" :max="99" class="item"><Icon type="ios-bell-outline" size = "28"></Icon></Badge></router-link></span>
          <span>
            <Dropdown>
                <span>
                  <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size = "large"/>
                </span>
                <DropdownMenu slot="list">
                  <DropdownItem><router-link to="/classes/setting"><Button type="text" size="small"><Icon type="android-settings"></Icon>个人设置</Button></router-link></DropdownItem>
                  <DropdownItem><Button type="text" size="small"><Icon type="android-download"></Icon>文库下载</Button></DropdownItem>
                  <DropdownItem><Button type="text" size="small" @click="logout"><Icon type="log-out"></Icon>退出登录</Button></DropdownItem>
                </DropdownMenu>
            </Dropdown>
          </span>
        </div>
      </div>
      <div id = "center">
        <h1>{{clazz.name}}</h1>
        <p>请使用以下邀请码邀请同学加入班级</p>
        <div>
        <Dropdown>
            <Button type = "text" style = "color:white;font-size:1.2rem;border:1px solid white">{{clazz.invitecode}}&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon></Button>
            <DropdownMenu slot="list">
                <DropdownItem>停用</DropdownItem>
                <DropdownItem>重置</DropdownItem>
            </DropdownMenu>
        </Dropdown>
        </div>
      </div>
      <div id = "bottom">
        <div><a href = "javascript:void(0)"  @click = "changeBagModal = true">选择主题图片</a></div>
        <div>
          <span>考勤</span>
          <span>表现</span>
          <span>成员</span>
          <span>成绩</span>
        </div>
      </div>
    </header>
    </transition>
    <nav>
      <a href = "javascript:void(0)"><span @click = "to('homework')" :class = "{'nav-selected':navSelected == 'homework'}">作业</span></a>
      <a href = "javascript:void(0)"><span @click = "to('topic')" :class = "{'nav-selected':navSelected == 'topic'}">话题</span></a>
      <a href = "javascript:void(0)"><span @click = "to('sharefile')" :class = "{'nav-selected':navSelected == 'sharefile'}">资料</span></a>
      <a href = "javascript:void(0)"><span @click = "to('bulletin')" :class = "{'nav-selected':navSelected == 'bulletin'}">公告</span></a>
      <a href = "javascript:void(0)"><span @click = "to('test')" :class = "{'nav-selected':navSelected == 'test'}">测试</span></a>
    </nav>
    <div id = "main">
      <v-drawer></v-drawer>
      <div id = "main-container">
        <transition name="slide-fade">
        <router-view @headerhidden = "headerShow = false" @headershow = "headerShow = true" ></router-view>
        </transition>
      </div>
    </div>
    <div class = "modals" style = "display:hidden">
      <Modal
        v-model="changeBagModal"
        :scrollable = "true"
        title="选择主题图片"
        :mask-closable = "false"
        :width = "80"
        >
        <div class = "bacg-picker">
          <img src = "../assets/images/headerbackg/01.jpg"/>
          <img src = "../assets/images/headerbackg/02.jpg"/>
          <img src = "../assets/images/headerbackg/03.jpg"/>
          <img src = "../assets/images/headerbackg/04.jpg"/>
        </div>
      </Modal>
    </div>
    <v-footer></v-footer>
  </div>
</template>
<script>
import drawer from '@/components/common/drawer'
import footer from '@/components/common/footer'
import homework from '@/components/classroom/homework'
import submithomework from '@/components/classroom/submithomework'
import topic from '@/components/classroom/topic'
import sharefile from '@/components/classroom/sharefile'
import 'static/editor/ueditor.config.js'
import 'static/editor/ueditor.all.min.js'
import 'static/editor/lang/zh-cn/zh-cn.js'
export default({
  name:'classroom',
  data(){
    return {
      headerShow:true,
      changeBagModal:false

    }
  },
  components:{
    'v-footer':footer,
    'v-drawer':drawer
  },
  created () {
    if(!this.$store.getters.getCurrentClass || this.$store.getters.getCurrentClass == null){
      this.$router.push('/classes');
    }
  },
  methods:{
    to(targ){
      this.headerShow = true;
      this.$router.push('/classroom/' + targ);
    },
    logout(){
      var that = this;
      this.$axios.get(this.$apiPrefix + 'logout')
      .then(function(res){
        if(res.data.success == true){
          that.$router.push('/login');
        }
      })
      .catch(function(e){
        console.log(e);
      });
    }

  },
  computed:{
    clazz(){return this.$store.getters.getCurrentClass},
    navSelected(){
      console.log(this.$store.getters.getCurrentNav);
      return this.$store.getters.getCurrentNav;
    }
  },
  homework:homework,
  submithomework:submithomework,
  topic:topic,
  sharefile:sharefile
})
</script>
<style lang = "less" scoped>
@bor-c:#c8c8c8;
header {
  position:relative;
  height: 20rem;
  padding:1rem 2rem;
  background-color:rgba(255, 241, 250, 0.7);
  background-image:url(../assets/images/headerbackg/03.jpg);
	background-size:cover;
  #menubar {
    display: flex;
    justify-content:space-between;
    align-items: center;

    #user-option {
      padding:0 2rem;
      display: flex;
      align-items: center;
      > span {
        margin-left:3rem;
      }

    }
  }
  #center {
    color:white;
    text-align: center;
    height: 14rem;
    display: flex;
    flex-direction: column;
    justify-content: center ;
    align-items:center;
    > p, div {
      margin-top:1rem;
    }
  }
  #bottom {
    color:white;
    padding:1rem 2rem;
    display: flex;
    justify-content:space-between;
    position: absolute;
    left:0;
    bottom:0;
    right:0;
    > div + div span {
      margin-left:2rem;

    }

  }
}
nav {
  height: 4rem;
  z-index:1;
	color:#eeedee;
	font-size:1.1rem;
	background-color:#834a79;
	min-height:4rem;
  text-align:center;
  
  span:nth-child(n) {
    display:inline-block;
    height:inherit;
    width:8rem;
    margin:0 .5rem;
    line-height:3.8rem;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    &:hover {
      border-bottom:1px solid white;
    }
  }
}
.nav-selected {
	border-bottom:1px solid white;
}
#main {
  height:50rem;
  background-color:#e7e4f0;
  overflow-x:hidden;
  width:100vw;
  padding-right: 20px;
 
}
#main-container {
  height:30rem;
  width:70%;
  margin:0 auto;
  
}
/*header背景选择对话*/
.bacg-picker {
  width:100%;
  z-index: inherit;
  img {
    opacity: .6;
    width:auto;
    height:auto;
    max-width:40%;
    max-height:20%;
    margin:.5rem;
    &:hover {
      opacity:1;
    }
    
  }

}

/*header过渡效果*/
.slide-fade-enter-active {
  transition: all 2s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  /*transform: translateX(10px);*/
  opacity: 0;
}

</style>


